@media (max-width: 768px){.navbar-static-sm{position:absolute}}.navbar-brand svg{transition:fill .2s linear}.navbar-toggle{position:fixed;right:50px;display:block;padding:5px 0 0 0;margin:0;border:0}.navbar-toggle .icon-bar{position:relative;background-color:white;width:19px;transition:transform .2s ease-in-out, background-color .2s linear}.navbar-toggle:hover{transform:translate(0, 0)}.navbar-toggle:hover .icon-bar-up{transform:translate(0, -3px)}.navbar-toggle:hover .icon-bar-down{transform:translate(0, 3px)}.navbar-toggle-replace.shown+.navbar-toggle{display:none}.navbar>.container .navbar-brand,.navbar>.container-fluid .navbar-brand{margin-left:0}.navbar-toggle-replace{position:fixed;right:50px;float:right;opacity:0;max-width:0;padding-left:0;padding-right:0;overflow:hidden;transition:all 0.2s ease-out;visibility:hidden}.navbar-toggle-replace.shown{max-width:200px;visibility:visible;padding-left:18px;padding-right:18px;opacity:1}.navbar-color-primary .navbar-toggle .icon-bar{background-color:#00c092}.navbar-color-primary .navbar-brand svg{fill:#00c092}.navbar-click-through{pointer-events:none}.navbar-click-through .nav{pointer-events:auto}.navbar-brand{margin:0;padding:0;height:auto;position:fixed;left:50px}@media (max-width: 480px){.navbar-brand,.navbar-toggle-replace,.navbar-toggle{position:static}}.navbar-padded{padding:50px 35px 0 35px;margin:0;border:0}.navbar-padded .login-btn{margin-right:30px;margin-top:-5px}@media (max-width: 768px){.navbar-padded .login-btn{margin-right:45px}}@media (max-width: 480px){.navbar-padded .login-btn{display:none}}@media (max-width: 768px){.navbar-padded{padding:20px 20px 0 20px}}
